import { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { useCookies } from "react-cookie";
import { url } from "../const";
import { Header } from "../components/Header";
import "./Home.css";
import ReactPaginate from "react-paginate";
import { useSelector, useDispatch } from "react-redux/es/exports";
import { useNavigate } from "react-router-dom";
import { signOut } from "../authSlice";

import { NewBooks } from "./NewBooks"

//
export const Home = () => {
  const [lists, setLists] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");
  const [cookies] = useCookies();
  const [selectPage, setSelectPage] = useState(0);

  useEffect(() => {
    axios
      .get(`${url}/books?offset=${selectPage}`, {
        //selectPage
        headers: {
          authorization: `Bearer ${cookies.token}`,
        },
      })
      .then((res) => {
        setLists(res.data);
      })
      .catch(() => {
        axios
          .get(`${url}/public/books?offset=${selectPage}`)
          .then((res) => {
            setLists(res.data);
          })
          .catch((err) => {
            setErrorMessage(`${err}`);
          });
      });
  }, [selectPage]);

  const listLength = 30;
  //API
  //API
  const displayCount = 10;

  const curtIndex = selectPage;


  const handlePageClick = (page) => {
    setSelectPage(page.selected * 10, "selectPage");
  };

  const pageCountCalc = (len, count) => {
    if (len < count) {
      return 1;
    }
    return Math.ceil(len / count);
  };

  const [selectBookId, setSelectBookId] = useState("");
  //post
  const onHandlelinkclick = () => {
    axios
      .post(
        `${url}/logs`,
        {},
        {
          headers: {
            Authorization: `Bearer ${cookies.token}`,
          },
        }
      )
      .then((res) => {
        setSelectBookId(res);
      })
      .catch((err) => {
      });
  };

  // 
  var day = new Date(document.lastModified);
  var y = day.getFullYear();
  var m = day.getMonth() + 1;
  var d = day.getDate();

  // 
  var week = new Array("", "", "", "", "", "", "");
  var w = week[day.getDay()];

  //
  if (m < 10) m = "0" + m;
  if (d < 10) d = "0" + d;

  const [name, setName] = useState();
  const [iconURL, setIconURL] = useState();
  const auth = useSelector((state) => state.auth.isSignIn);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const handleSignOut = () => {
    dispatch(signOut());
    navigate("/signin");
  };

  useEffect(() => {
    axios
      .get(`${url}/users`, {
        headers: {
          Authorization: `Bearer ${cookies.token}`,
        },
      })
      .then((res) => {
        setName(res.data.name);
        setIconURL(res.data.iconUrl);
      })
      .catch((err) => {
      });
  }, []);

  const [showModal, setShowModal] = useState(false);
  const ShowModal = () => {
    setShowModal(true);
  };


  return (
    <div>
      <Header />

      <div className="Block">

        <div className="Block__side">
          <div className="user-profiles">
            <div className="user-profiles__avatar">
              <img src={iconURL} width="70" height="70" />
            </div>

            <div className="user-profiles__details">
              <div className="details__name">{name}</div>
              <div className="details__btn"></div>
            </div>
          </div>

          <div></div>
          <div></div>
          <div></div>
          <div></div>
        </div>
        <div className="Block__main">
          <p className="title-content"></p>
          <p className="error-message">{errorMessage}</p>
          <button onClick={ShowModal}></button>
          <NewBooks showFlag={showModal} setShowModal={setShowModal} />


          <div className="Block__element">
            {" "}
            <ul>
              {lists.map((list) => {
                return (
                  <li key={list.id} className="Block__element--modifier">
                    <div className="frame__header">
                      <span className="reviewer">{list.reviewer}</span>
                    </div>
                    <Link to={`/books/${list.id}`} onClick={onHandlelinkclick}>
                      {list.title}
                    </Link>
                    <div className="frame__details">
                      <span className="">
                        <span> </span> {y}  {m}  {d}  ( {w} )
                      </span>
                    </div>
                  </li>
                );
              })}
            </ul>
            <ReactPaginate
              style={{ marginTop: 10, marginBottom: 100 }}
              pageCount={pageCountCalc(listLength, displayCount)} // 
              onPageChange={handlePageClick}
              marginPagesDisplayed={5} // 
              pageRangeDisplayed={10} // 
              containerClassName="pagination" // ul(pagination)
              pageClassName="page-item" // li
              pageLinkClassName="page-link" // a
              activeClassName="active" // active.li
              previousLabel="<" // a
              nextLabel=">" // a
              // 
              previousClassName="page-item"
              nextClassName="page-item"
              previousLinkClassName="page-link"
              nextLinkClassName="page-link"
              //  or ()
              disabledClassName="disabled"
              // 
              breakLabel="..."
              breakClassName="page-item"
              breakLinkClassName="page-link"
            />
          </div>
        </div>
      </div>
    </div>
  );
};
